<template>
    <div class="gallery container">
        <div class="categories">
            <div class="categories__wrapper">
                <ul class="categories__list">
                    <li class="categories__item" v-for="category in categories"
                        :class="{ 'categories__item--active': category.url == getUrl }">
                        <a href="#" @click.prevent="changeUrl(category.url)" @click="currentCategory = category.id">{{ category.text }}</a>
                    </li>
                </ul>
            </div>
        </div>

        <div class="filter" :class="{ 'filter--hidden': !sectionActive.filters }">
            <button class="filter__hidden-btn" @click.prevent="sectionActive.filters = !sectionActive.filters">Фильтры
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 17 17">
                    <path d="M8.2859 8.0043H.4779A.4758.4758 0 000 8.4822c0 .2654.2124.4778.4778.4778h7.808c.223 1.1078 1.2035 1.9467 2.3786 1.9467s2.1555-.8389 2.3785-1.9467h3.4757a.4759.4759 0 00.4779-.4779.4759.4759 0 00-.4779-.4778h-3.4757c-.223-1.1078-1.2034-1.9467-2.3785-1.9467-1.1716 0-2.1555.8353-2.3785 1.9467zm3.8474.4779c0 .8105-.6584 1.4688-1.4689 1.4688-.8106 0-1.4689-.6583-1.4689-1.4688 0-.8106.6583-1.469 1.4689-1.469.8105 0 1.4689.6584 1.4689 1.469zM4.2792 13.8551H.4778A.4759.4759 0 000 14.333c0 .2654.2124.4778.4778.4778h3.8014c.223 1.1078 1.2034 1.9467 2.3785 1.9467 1.1751 0 2.1555-.8389 2.3785-1.9467h7.4824a.4759.4759 0 00.4779-.4778.476.476 0 00-.4779-.4779h-7.486c-.2229-1.1078-1.2033-1.9467-2.3784-1.9467s-2.152.8389-2.375 1.9467zm3.8474.4779c0 .8105-.6583 1.4688-1.4689 1.4688-.8105 0-1.4689-.6583-1.4689-1.4688 0-.8106.6584-1.4689 1.469-1.4689.8105 0 1.4688.6619 1.4688 1.4689zM4.2792 2.1891H.4778A.4758.4758 0 000 2.667c0 .2655.2124.4779.4778.4779h3.8014c.223 1.1078 1.2034 1.9467 2.3785 1.9467 1.1751 0 2.1555-.8389 2.3785-1.9467h7.486A.4759.4759 0 0017 2.6669a.4758.4758 0 00-.4778-.4778H9.0327C8.8097 1.0813 7.8293.2424 6.6542.2424c-1.1751 0-2.152.8353-2.375 1.9467zm3.8474.4778c0 .8106-.6583 1.469-1.4689 1.469-.8105 0-1.4689-.6584-1.4689-1.469 0-.8105.6584-1.4688 1.469-1.4688.8105 0 1.4688.6583 1.4688 1.4688z"/>
                </svg>
            </button>
            <div class="filter__head">
                <h3 class="filter__name">{{ filters.work.name }}</h3>
                <button :class="{pressed: sectionActive.work}" class="filter__btn"
                        @click.prevent="sectionActive.work = !sectionActive.work"></button>
            </div>
            <ul class="filter__list" :class="{ 'filter__list--collapse': filters.work.items.length > 7 && collapseLists.work}"
                v-if="sectionActive.work">
                <li ref="work" class="filter__item" v-for="item in filters.work.items">
                    <label class="filter__label">
                        <input class="filter__checkbox" type="checkbox" v-bind:value="item.value"
                               v-model="selectedFilters.work"/>{{ item.title }}
                        <span class="filter__checkbox-indicator">+</span>
                    </label>
                </li>
                <li class="filter__collapse" v-if="filters.work.items.length > 7" @click="collapseLists.work=!collapseLists.work" :class="{ 'pressed': !collapseLists.work}">
                    <span v-if="collapseLists.work">Все стили</span><span v-if="!collapseLists.work">свернуть</span>
                </li>
            </ul>
            <div class="filter__head">
                <h3 class="filter__name">{{ filters.plot.name }}</h3>
                <button :class="{ pressed: sectionActive.plot }" class="filter__btn"
                        @click.prevent="sectionActive.plot = !sectionActive.plot"></button>
            </div>
            <ul class="filter__list" :class="{ 'filter__list--collapse': filters.plot.items.length > 7 && collapseLists.plot}"
                v-if="sectionActive.plot">
                <li>
                    <input class="filter__search" type="text" placeholder="Быстрый поиск"/>
                </li>
                <li ref="plot" class="filter__item" v-for="item in filters.plot.items">
                    <label class="filter__label">
                        <input class="filter__checkbox" type="checkbox" v-bind:value="item.value"
                               v-model="selectedFilters.plot"/>{{ item.title }}
                        <span class="filter__checkbox-indicator">+</span>
                    </label>
                </li>
                <li class="filter__collapse" v-if="filters.plot.items.length > 7" @click="collapseLists.plot=!collapseLists.plot" :class="{ 'pressed': !collapseLists.plot}">
                    <span v-if="collapseLists.plot">Все стили</span><span v-if="!collapseLists.plot">свернуть</span>
                </li>
            </ul>
            <div class="filter__head">
                <h3 class="filter__name">{{ filters.styles.name }}</h3>
                <button :class="{ pressed: sectionActive.styles }" class="filter__btn"
                        @click.prevent="sectionActive.styles = !sectionActive.styles"></button>
            </div>
            <ul class="filter__list" :class="{ 'filter__list--collapse': filters.styles.items.length > 7 && collapseLists.styles}"
                v-if="sectionActive.styles">
                <li ref="styles" class="filter__item" v-for="item in filters.styles.items">
                    <label class="filter__label">
                        <input class="filter__checkbox" type="checkbox" v-bind:value="item.value"
                               v-model="selectedFilters.styles"/>{{ item.title }}
                        <span class="filter__checkbox-indicator">+</span>
                    </label>
                </li>
                <li class="filter__collapse" v-if="filters.styles.items.length > 7" @click="collapseLists.styles=!collapseLists.styles" :class="{ 'pressed': !collapseLists.styles}">
                    <span v-if="collapseLists.styles">Все стили</span><span v-if="!collapseLists.styles">свернуть</span>
                </li>
            </ul>
            <div class="filter__head">
                <h3 class="filter__name">{{ filters.technics.name }}</h3>
                <button :class="{ pressed: sectionActive.technics }" class="filter__btn"
                        @click.prevent="sectionActive.technics = !sectionActive.technics"></button>
            </div>
            <ul class="filter__list" :class="{ 'filter__list--collapse': filters.technics.items.length > 7 && collapseLists.technics}"
                v-if="sectionActive.technics">
                <li>
                    <input class="filter__search" type="text" placeholder="Быстрый поиск"/>
                </li>
                <li ref="technics" class="filter__item" v-for="item in filters.technics.items">
                    <label class="filter__label">
                        <input class="filter__checkbox" type="checkbox" v-bind:value="item.value"
                               v-model="selectedFilters.technics"/>{{ item.title }}
                        <span class="filter__checkbox-indicator">+</span>
                    </label>
                </li>
                <li class="filter__collapse" v-if="filters.technics.items.length > 7" @click="collapseLists.technics=!collapseLists.technics" :class="{ 'pressed': !collapseLists.technics}">
                    <span v-if="collapseLists.technics">Все стили</span><span v-if="!collapseLists.technics">свернуть</span>
                </li>
            </ul>
            <div class="filter__head">
                <h3 class="filter__name">{{ filters.period.name }}</h3>
                <button :class="{ pressed: sectionActive.period }" class="filter__btn"
                        @click.prevent="sectionActive.period = !sectionActive.period"></button>
            </div>
            <ul class="filter__list" :class="{ 'filter__list--collapse': filters.period.items.length > 7 && collapseLists.period}"
                v-if="sectionActive.period">
                <li>
                    <div class="filter__search-container">
                        <input class="filter__search filter__search--date filter__search--min" type="number"
                               placeholder="с" min="1896" max="1926" v-model="min">
                        <input class="filter__search filter__search--date" type="number" placeholder="по" max="1927"
                               :min="min">
                    </div>
                </li>
                <li ref="period" class="filter__item" v-for="item in filters.period.items">
                    <label class="filter__label">
                        <input class="filter__checkbox" type="checkbox" v-bind:value="item.value"
                               v-model="selectedFilters.period"/>{{ item.title }}
                        <span class="filter__checkbox-indicator">+</span>
                    </label>
                </li>
                <li class="filter__collapse" v-if="filters.period.items.length > 7" @click="collapseLists.period=!collapseLists.period" :class="{ 'pressed': !collapseLists.period}">
                    <span v-if="collapseLists.period">Все стили</span><span v-if="!collapseLists.period">свернуть</span>
                </li>
            </ul>
        </div>

        <div class="pictures">
            <div class="pictures__container" :class="{ 'pictures__container--small': smallCards }">
                <div class="pictures__header">
                    <ul class="pictures__filter-list">
                        <li class="pictures__filter-item" v-for="item in activeFilterIcons">{{ item }}
                            <button @click="deleteFilter" :value="item">&times;</button>
                        </li>
                    </ul>
                    <div class="pictures__toggles">
                        <button class="pictures__toggle" :class="{ 'pictures__toggle--active': !smallCards }"
                                @click="makeSmallCard(false)">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 15 15">
                                <path d="M0 0h6v6H0zM9 0h6v6H9zM0 9h6v6H0zM9 9h6v6H9z"/>
                            </svg>
                        </button>
                        <button class="pictures__toggle" :class="{ 'pictures__toggle--active': smallCards }"
                                @click="makeSmallCard(true)">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 15 15">
                                <path fill-rule="evenodd"
                                      d="M0 0h2.647v2.647H0V0zm0 6.176h2.647v2.648H0V6.176zm0 6.177h2.647V15H0v-2.647zM6.176 0h2.648v2.647H6.176V0zm0 6.176h2.648v2.648H6.176V6.176zm0 6.177h2.648V15H6.176v-2.647zM12.353 0H15v2.647h-2.647V0zm0 6.176H15v2.648h-2.647V6.176zm0 6.177H15V15h-2.647v-2.647z"
                                      clip-rule="evenodd"/>
                            </svg>
                        </button>
                    </div>
                </div>
                <ul class="pictures__list">
                    <li class="pictures__item" v-for="item in filteredProducts">
                        <img class="pictures__image" v-bind:src="item.img" v-bind:alt="item.name">
                        <p class="pictures__name">{{ item.name }} <span>{{ item.year }}</span></p>
                    </li>
                </ul>
            </div>
            <nav class="pagination" :class="{ 'pagination--small': smallCards }">
                <button class="pagination__btn" v-for="pageNumber in collection" @click.prevent="setPage(pageNumber)"
                        :class="{ 'active': pageNumber === pagination.currentPage }">{{pageNumber}}
                </button>
            </nav>
        </div>

    </div>
</template>

<script>
    import axios from "axios";
    import _ from 'lodash';

    export default {
        name: "Gallery",

        data() {
            return {
                getUrl: '/data/Rarity.json',
                selectedFilters: {
                    work: [],
                    plot: [],
                    styles: [],
                    technics: [],
                    period: []
                },
                perPage: 10,
                pagination: {},
                items: [],
                filteredItems: [],
                currentCategory: 0,
                categories: [
                    {id: 1, text: 'Раритет', url: '/data/Rarity.json'},
                    {id: 2, text: 'Новые', url: '/data/New.json'},
                    {id: 3, text: 'Антиквариат', url: '/data/Antiques.json'},
                    {id: 4, text: 'Филателия', url: '/data/Philately.json'},
                ],
                filters: {
                    work: {
                        name: 'работы',
                        model: 'work',
                        items: [{
                            title: 'Живопись',
                            value: 'painting'
                        },
                            {
                                title: 'Рисунки и иллюстрации',
                                value: 'drawings and illustrations'
                            },
                            {
                                title: 'Театрально-декорационное',
                                value: 'theatrical decoration'
                            },
                            {
                                title: 'Графика',
                                value: 'graphics'
                            },
                            {
                                title: 'Гравюра',
                                value: 'engraving'
                            },
                            {
                                title: 'Плакат',
                                value: 'poster'
                            },
                            {
                                title: 'Скульптура',
                                value: 'sculpture'
                            },
                            {
                                title: 'Декоративно-прикладное',
                                value: 'decorative and applied'
                            }]
                    },

                    plot: {
                        name: 'сюжет',
                        model: 'plot',
                        items: [{
                            title: 'Портрет',
                            value: 'portrait'
                        },
                            {
                                title: 'Жанровая сцена',
                                value: 'genre scene'
                            },
                            {
                                title: 'Пейзаж',
                                value: 'scenery'
                            },
                            {
                                title: 'Литературная сцена',
                                value: 'literary scene'
                            },
                            {
                                title: 'Городской пейзаж',
                                value: 'city landscape'
                            },
                            {
                                title: 'Ню',
                                value: 'nude'
                            },
                            {
                                title: 'Интерьер',
                                value: 'interior'
                            },
                            {
                                title: 'Натюрморт',
                                value: 'still life'
                            },
                            {
                                title: 'Аллегорическая сцена',
                                value: 'allegorical scene'
                            },
                            {
                                title: 'Историческая сцена',
                                value: 'historical scene'
                            },
                            {
                                title: 'Архитектура',
                                value: 'architecture'
                            },
                            {
                                title: 'Анимализм',
                                value: 'animalism'
                            },
                            {
                                title: 'Религиозная сцена',
                                value: 'religious scene'
                            }]
                    },

                    styles: {
                        name: 'стили',
                        model: 'styles',
                        items: [
                            {
                                title: 'Реализм',
                                value: 'realism'
                            },
                            {
                                title: 'Модерн',
                                value: 'modern'
                            },
                            {
                                title: 'Импрессионизм',
                                value: 'impressionism'
                            },
                            {
                                title: 'Соцреализм',
                                value: 'socialist realism'
                            },
                            {
                                title: 'Примитивизм',
                                value: 'primitivism'
                            },
                            {
                                title: 'Ар Нуво',
                                value: 'art nouveau'
                            },
                            {
                                title: 'Сюрреализм',
                                value: 'surrealism'
                            }]
                    },

                    technics: {
                        name: 'техника',
                        model: 'technics',
                        items: [{
                            title: 'Масло',
                            value: 'oil'
                        },
                            {
                                title: 'Акварель',
                                value: 'watercolor'
                            },
                            {
                                title: 'Графитный карандаш',
                                value: 'lead pencil'
                            },
                            {
                                title: 'Гуашь',
                                value: 'gouache'
                            },
                            {
                                title: 'Пастель',
                                value: 'pastel'
                            },
                            {
                                title: 'Тушь',
                                value: 'ink'
                            },
                            {
                                title: 'Графит',
                                value: 'graphite'
                            },
                            {
                                title: 'Цветной карандаш',
                                value: 'color pencil'
                            },
                            {
                                title: 'Уголь',
                                value: 'coal'
                            },
                            {
                                title: 'Мел',
                                value: 'a piece of chalk'
                            },
                            {
                                title: 'Гравюра',
                                value: 'engraving'
                            },
                            {
                                title: 'Перо',
                                value: 'pen'
                            }]
                    },

                    period: {
                        name: 'период',
                        model: 'period',
                        items: [{
                            title: 'до 1900',
                            value: [1878, 1879, 1880, 1881, 1882, 1883, 1884, 1885, 1886, 1887, 1888, 1889, 1890, 1891, 1892, 1893, 1894, 1895, 1896, 1897, 1898, 1899, 1900]
                        },
                            {
                                title: '1901-1916',
                                value: [1901, 1902, 1903, 1904, 1905, 1906, 1907, 1908, 1909, 1910, 1911, 1912, 1913, 1914, 1915, 1916]
                            },
                            {
                                title: '1917 и позже',
                                value: [1917, 1918, 1919, 1920, 1921, 1922, 1923, 1924, 1925, 1926, 1927]
                            }]
                    }
                },
                sectionActive: {
                    filters: true,
                    work: true,
                    plot: true,
                    styles: true,
                    technics: true,
                    period: true
                },
                smallCards: false,
                min: '',
                collapseLists: {
                    filters: true,
                    work: true,
                    plot: true,
                    styles: true,
                    technics: true,
                    period: true
                },
            }
        },

        methods: {
            setPage(p) {
                this.pagination = this.calculatePaginationProperties(this.items.length, p);
            },
            paginate(data) {
                return _.slice(data, this.pagination.startIndex, this.pagination.endIndex + 1)
            },
            calculatePaginationProperties(totalItems, currentPage) {
                const startIndex = (currentPage - 1) * this.perPage,
                    endIndex = Math.min(startIndex + this.perPage - 1, totalItems - 1);
                return {
                    currentPage: currentPage,
                    startIndex: startIndex,
                    endIndex: endIndex,
                    pages: _.range(1, Math.ceil(totalItems / this.perPage) + 1)
                };
            },
            deleteFilter(event) {
                let arr;
                for (let key in this.selectedFilters) {
                    this.filters[key].items.filter(product => {
                        return event.target.value.includes(product.title)
                    }).forEach(product => {
                        arr = product.value
                    });
                    if (this.selectedFilters[key].includes(arr)) this.selectedFilters[key].splice(this.selectedFilters[key].indexOf(arr), 1);
                }
            },
            changeUrl(url, index) {
                this.getUrl = url;
                this.getPosts(url);
                this.currentCategory = index;
            },
            getPosts() {
                axios.get(this.getUrl)
                    .then(response => {
                        this.items = response.data;
                    })
                    .then(() => {
                        this.setPage(1);
                    })
            },
            makeSmallCard(boolean) {
                boolean ? this.smallCards = true : this.smallCards = false;
                boolean ? this.perPage = 12 : this.perPage = 10;
                this.setPage(this.pagination.currentPage);
            },
        },

        computed: {
            filteredProducts() {
                this.filteredItems = this.items
                    .filter(product => {
                        return this.selectedFilters.work == 0 || this.selectedFilters.work.includes(product.work);
                    })
                    .filter(product => {
                        return this.selectedFilters.plot == 0 || this.selectedFilters.plot.includes(product.plot);
                    })
                    .filter(product => {
                        return this.selectedFilters.styles == 0 || this.selectedFilters.styles.includes(product.style);
                    })
                    .filter(product => {
                        return this.selectedFilters.technics == 0 || this.selectedFilters.technics.includes(product.technics);
                    })
                    .filter(product => {
                        return this.selectedFilters.period == 0 || _.flatten(this.selectedFilters.period).includes(product.year) //.includes(product.period);
                    });
                return this.paginate(this.filteredItems);
            },
            collection() {
                return _.range(1, Math.ceil(this.filteredItems.length / this.perPage) + 1)
            },
            activeFilterIcons() {
                let arr = [];
                for (let key in this.selectedFilters) {
                    this.filters[key].items.filter((product, index) => {
                        return this.selectedFilters[key].includes(product.value)
                    }).forEach(product => {
                        arr.push(product.title)
                    })
                }
                return arr;
            }
        },
        created() {
            this.getPosts(this.getUrl);
        },
    }
</script>

<style lang="scss">
    .gallery {
        overflow: visible;
        padding-top: 20px;
        padding-bottom: 55px;
    }

    .categories {
        width: 100%;
        overflow: hidden;
    }

    .categories__wrapper {
        width: auto;
        overflow-x: auto;
        margin: 0 0 -20px;
        padding: 0 0 20px 20px;
    }

    .categories__list {
        display: inline-block;
        vertical-align: top;
        font-size: 0;
        white-space: nowrap;
        list-style: none;
        padding: 0;
        margin: 0 0 38px;
    }

    .categories__item {
        display: inline-block;
        font-family: Yeseva One, serif;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
        padding: 9px 0;
        margin-right: 21px;

        > a {
            color: #C4C4C4;
        }

        &--active {
            border-bottom: 3px solid #202020;

            > a {
                color: #202020;
            }
        }
    }

    .filter {
        padding: 0 20px;

        &--hidden {
            @media (max-width: 1023px) {
                .filter__head,
                .filter__list {
                    display: none;
                }
            }
        }
    }

    .filter__hidden-btn {
        position: relative;
        width: 100%;
        font-size: 12px;
        line-height: 26px;
        text-align: left;
        background: none;
        border: 1px solid #E5E5E5;
        cursor: pointer;
        padding: 0 14px;

        > svg {
            position: absolute;
            top: 4px;
            right: 10px;
            width: 17px;
            height: 17px;
        }
    }

    .filter__head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        margin: 7px 0 8px;
    }

    .filter__name {
        font-size: 12px;
        line-height: 14px;
        text-transform: uppercase;
    }

    .filter__btn {
        position: relative;
        width: 10px;
        height: 10px;
        border: none;
        background: none;
        padding: 0;

        &:before,
        &:after {
            content: "";
            position: absolute;
            background-color: #202020;
            transition: transform 0.25s ease-out;
        }

        &:before {
            top: 0;
            left: 50%;
            width: 2px;
            height: 100%;
            margin-left: -1px;
        }

        &:after {
            top: 50%;
            left: 0;
            width: 100%;
            height: 2px;
            margin-top: -1px;
        }

        &.pressed {
            cursor: pointer;

            &:before {
                transform: rotate(90deg);
            }

            &:after {
                transform: rotate(180deg);
            }
        }
    }

    .filter__list {
        list-style: none;
        border-bottom: 1px solid #E5E5E5;;
        margin: 0;
        padding: 0 0 20px 0;

        &--collapse .filter__item:nth-child(n+8) {
            display: none;
        }
    }

    .filter__item {
        font-size: 12px;
        line-height: 32px;

        input {
            display: none;
        }
    }

    .filter__search-container {
        display: flex;
        width: 170px;
    }

    .filter__search {
        width: 100%;
        border: 1px solid #E5E5E5;
        padding: 7px;
        margin-bottom: 13px;

        &::placeholder {
            font-size: 12px;
            line-height: 26px;
            display: flex;
            align-items: center;
            color: #F5F5F5;
        }

        &--min {
            margin-right: 10px;
        }
    }

    .filter__label {
        position: relative;
    }

    .filter__label input[type="checkbox"] + .filter__checkbox-indicator {
        position: absolute;
        top: -9px;
        left: -15px;
        display: none;
        transition: transform 0.25s ease-out;
    }

    .filter__label:hover input[type="checkbox"]:hover + .filter__checkbox-indicator {
        display: inline;
    }

    .filter__label input[type="checkbox"]:checked + .filter__checkbox-indicator {
        display: inline;
        transform: rotate(45deg);
    }

    .filter__collapse {
        position: relative;
        font-size: 12px;
        font-weight: bold;
        line-height: 26px;
        color: #202020;
        cursor: pointer;
        padding-left: 20px;

        &:before,
        &:after {
            content: "";
            position: absolute;
            background-color: #202020;
            transition: transform 0.25s ease-out;
        }

        &:before {
            top: 9px;
            left: 4px;
            width: 2px;
            height: 10px;
        }

        &:after {
            top: 13px;
            left: 0;
            width: 10px;
            height: 2px;
        }

        &.pressed {
            cursor: pointer;

            &:before {
                transform: rotate(90deg);
            }

            &:after {
                transform: rotate(180deg);
            }
        }
    }

